import * as React from "react";
import Navigation from "src/components/Navigation/Navigation";
import styled from "styled-components";

export interface VideoProps {}
export interface VideoState {
    videos: string[];
}

const Content = styled.div`
    width: 100%;
    padding-bottom: 200px;
    font-size: 14px;

    min-height: 100vh;
`;

const Desc = styled.div`
    padding: 0 15%;
    margin-top: 62px;
    & > div {
        display: flex;
        margin-bottom: 34px;
        & > div:nth-child(1) {
            width: calc(20% - 45px);
            margin-right: 45px;
            font-size: 18px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.61;
        }
        & > div:nth-child(2) {
            width: 80%;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }
    }
`;

const VideoItem = styled.div`
    width: 30%;
    height: 0;
    padding-top: 20%;
    margin-bottom: 5%;
    position: relative;

    & > iframe {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
    }
`;
export default class Video extends React.Component<VideoProps, VideoState> {
    constructor(props: VideoProps) {
        super(props);
        this.state = {
            videos: ["04IngVJuZk8", "Kn91U_q4P1g", "GOdJaRa3Fgc", "nP3cBwtWR00", "JzHEML608ek", "lkO8WOBUCao", "69La9vXkDyc"]
        };
    }
    public render() {
        return (
            <>
                <Navigation
                    list={[
                        { name: "홈", link: "/" },
                        { name: "홍보", link: "/promotion" },
                        { name: "브랜드 영상", link: "/promotion/video" }
                    ]}
                />
                <Content>
                    <Desc>
                        <div>
                            <div>브랜드 영상</div>
                            <div>
                                {/* kMb-pBDcSoc */}
                                {this.state.videos.map(video => (
                                    <VideoItem key={video}>
                                        <iframe
                                            width="100%"
                                            height="100%"
                                            src={`https://www.youtube.com/embed/${video}`}
                                            allowFullScreen={true}
                                        />
                                    </VideoItem>
                                ))}
                            </div>
                        </div>
                    </Desc>
                </Content>
            </>
        );
    }
}



